  </section>
</template>

<script>
import { mapState } from "vuex";
import "../assets/css/Animate.css";


export default {
  name:'Section',
  components: {
    
  },
  data: function() {
    return {
      navAtBottom: false,
      isLoaded: false,
      productData: {
        homeHmenuButtons: []
      },
      isActive: "einstieg",
      section_title: "",
      navData: [],
      einstiegData: {},
      productsListData: {},
      tipsData: {},
      videoListData: {},
      videos: false,
      smarteIdeenListData: {},
      smarteIdeen: true,
      tariffListData: {},
      tariffs: false,
      security: false,
      securityListData: {},
      service: false,
      serviceData: {},
      products: false,
      tips: false
    };
  },
  mounted() {
    const productData = {
      homeHmenuButtons: []
    };
    this.productData = productData;
    this.isLoaded = true;
  },
  updated() {
    if (this.animatePage) this.isLoaded = true;
  },
  watch: {
    $route(to, from) {
      this.isActive = this.$route.params.pathMatch.split('/')[this.$route.params.pathMatch.split('/').length-1];
    }
  },
  methods: {
    buttonMenuClick(path) {
    }
  },

  created() {
    const _this = this;
    const {sectionName}=this.$route.params;
    const sectionData=this[sectionName];
        const {
          id,
          section_title,
          entry_type,
          entry_title,
          entry_text,
          popup,
          logo_title,
          logo_image_url,
          footer_image,
          products,
          products_list_bg_url,
          products_list_text,
          video_list_bg_url,
          video_list_text,
          videos,
          smarte_ideen_list_text,
          smarteIdeen,
          tariffs,
          tariffs_list_bg_url,
          tariffs_list_text,
          footer_icons,
          footer_title,
          tips,
          tips_list,
          tips_list_text,
          tips_list_image_url,
          tips_list_logo_url,
          tips_list_bg_url,
          tips_list_images,
          tips_list_json,
          service,
          service_list,
          service_images,
          service_list_text,
          service_list_bg_url,
          service_storer,
          service_storer_data,
          security,
          security_list_bg_url,
          security_list_text,
          security_storer,
          security_storer_data,
          entry_images
        } = sectionData.data;
        const navData = sectionData.navButtons;
        _this.section_title = section_title;
        _this.navData = navData;
        _this.einstiegData = {
          id,
          images: entry_images,
          entry_type,
          entry_title,
          entry_text,
          popup,
          logo_title,
          logo_image_url,
          footer_icons,
          footer_title
        };
        if (products == true) {
          _this.products = products;
          _this.productsListData = {
            products: sectionData.products,
            products_list_bg_url,
            products_list_text
          };
        }
        if (videos == true) {
          _this.videos = videos;
          _this.videoListData = {
            videos: sectionData.videos,
            video_list_bg_url,
            video_list_text
          };
        }
        if (smarteIdeen == true) {
          console.log("PLM ", smarteIdeen);
          
        _this.smarteIdeen = smarteIdeen;
          _this.smarteIdeenListData = {
            smarteIdeen: sectionData.smarteIdeen,
            smarte_ideen_list_text
          };
        }
        if (tariffs == true) {
          _this.tariffs = tariffs;
          const tariffsList = sectionData.data.tariffs_list;

          _this.tariffListData = {
            tariffsList,
            tariffs_list_text,
            tariffs_list_bg_url
          };
        }
        if (tips == true) {
          _this.tips = tips;
          _this.tipsData = {
            tips_list,
            tips_list_text,
            tips_list_image_url,
            tips_list_logo_url,
            tips_list_bg_url,
            tips_list_images,
            tips_list_json
          };
        }
        if (service == true) {
          _this.service = service;
          _this.serviceData = {
            service_list,
            service_images,
            service_list_text,
            service_list_bg_url,
            service_storer,
            service_storer_data
          };
        }
        if (security == true) {
          _this.security = security;
          const securityList = sectionData.data.security_list;

          _this.securityListData = {
            securityList,
            security_list_text,
            security_list_bg_url,
            security_storer,
            security_storer_data
          };
        }
  },
  computed: {
  ...mapState(['heimvernetzung',  'smart_home', 'zuhause','animatePage'])
  }
};
</script>
    
