<template>
  <section class="homeMenu">
    
  </section>
</template>

<script>
import { mapState } from "vuex";
import "../assets/css/Animate.css";
import { eventBus } from "./../main";
import { watch } from 'fs';
import { log } from 'util';
export default {
  components: {
  },
  data() {
    return {
      homeData: {
        description: "",
        background: "",
        logo: "",
        logo_title: ""
      },
      isLoaded: false,
      isBackground: false,
      isLogo: false,
      isLottie: false,
      defaultOptions: {},
      animationSpeed: 1,
      pathClick: "",
      LottieStyle: { right: "0" }
    };
  },
  created() {
    const home = this;
        const {
          description,
          background,
          logo,
          logo_title,
          buttons
        } = this.home;
        home.homeData = {
          description,
          background,
          logo,
          logo_title,
          buttons
        };

  },
  mounted() {
    const _this=this;
    
      eventBus.$on("hideScreensaverEvent", param => {
        _this.isLoaded = true;
        _this.isBackground = true;
        _this.isLogo= false;
        _this.isLottie= false;
      });
      eventBus.$on("showScreensaverEvent", param => {
        
        
      _this.isLoaded = false;
        _this.isBackground = false;
        _this.isLogo= false;
        _this.isLottie= false;
        _this.animationSpeed= 1;
         _this.$store.dispatch('activeDisplayMainMenuTitle');
        _this.pathClick= "";
      _this.LottieStyle= { right: "0" };

      
    });

    if (_this.animatePage)
    {
      this.isLoaded = true;
        this.isBackground = true;

    }
    eventBus.$on("mainButtonEvent", data => {
      this.$store.dispatch('deactivateDisplayMainMenuTitle');
      this.isBackground = false;
      this.isLogo = false;
      this.pathClick = data.path;
      this.defaultOptions = {
        animationData: JSON.parse(this.homeData.buttons[data.index].entry_json),
        loop: false
      };
      this.animationSpeed =  this.homeData.buttons[data.index].entry_speed;
    });

    eventBus.$on("mainButtonEventStartAnimation", () => {
      this.isLottie = true;
      this.isBackground = false;
    });
  },
  methods: {
    afterEnter: function(el) {
      if (!this.displayMainMenuTitle) {
        this.isLogo = true;
      } else {
        setTimeout(() => {
          this.isLogo = true;
        }, 700);
      }
    },
    onComplete: function(el) {
      console.log("onComplete");
    },
    animClick() {
      this.anim.goToAndPlay(this.anim.getDuration(true), true);
      
    },
    handleAnimation: function(anim) {
      this.anim = anim;
      this.anim.setSpeed(this.animationSpeed);
      
      const _this = this;
      setTimeout(() => {
        _this.isLottie= false;
      }, 4500);
      this.anim.addEventListener("complete", function(e) {
        //_this.isLottie = false;
        

        _this.$router.push(_this.pathClick);
        eventBus.$emit("mainButtonEnableEvent");
      });
    }
  },
  computed: {
  ...mapState(["home","animatePage","displayMainMenuTitle"])
  }
  
};
</script>

