import Vue from 'vue'
import Vuex from 'vuex'
const allData=require('./assets/allData.json');
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    animatePage: false,
    displayMainMenuTitle: true,
    productIndex: -1,
    prevButton:"",
    home: {...allData[0]},
    buttons: [...allData[1]],
    heimvernetzung: {...allData[2]},
    smart_home: {...allData[3]},
    zuhause: {...allData[4]}
  },
  mutations: {
    activeAnimatePage(state){
      state.animatePage=true;
    },
    deactivateAnimatePage(state){
      state.animatePage=false;
    },
    activeDisplayMainMenuTitle(state){
      state.displayMainMenuTitle=true;
    },
    deactivateDisplayMainMenuTitle(state){
      state.displayMainMenuTitle=false;
    },
    setProductIndex(state,{index}){
      state.productIndex=index;
    },
    setPrevButton(state,{prevButton}){
      state.prevButton=prevButton;
    }
  },
  actions: {
    activeAnimatePage(context){
      context.commit('activeAnimatePage')
    },
    deactivateAnimatePage(context){
      context.commit('deactivateAnimatePage')
    },
    activeDisplayMainMenuTitle(context){
      context.commit('activeDisplayMainMenuTitle')
    },
    deactivateDisplayMainMenuTitle(context){
      context.commit('deactivateDisplayMainMenuTitle')
    },
    setProductIndex(context,{index}){
      context.commit('setProductIndex',{index})
    },
    setPrevButton(context,{prevButton}){
      context.commit('setPrevButton',{prevButton})
    }
  }
})
